"use client";

import { useEffect, useState } from "react";
import api from "@/lib/axios";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import Link from "next/link";

interface Order {
    id: number;
    reference: string;
    recipient_phone: string;
    cost: number;
    status: string;
    bundle?: {
        name: string;
    };
}

export default function DashboardPage() {
    const [balance, setBalance] = useState(0);
    const [recentOrders, setRecentOrders] = useState<Order[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const walletRes = await api.get('/wallet');
                setBalance(walletRes.data.balance);

                const ordersRes = await api.get('/orders');
                setRecentOrders(ordersRes.data.data.slice(0, 5)); // Just take first 5
            } catch (e) {
                console.error(e);
            } finally {
                setLoading(false);
            }
        };
        fetchData();
    }, []);

    if (loading) return <div>Loading...</div>;

    return (
        <div className="space-y-6">
            <h2 className="text-3xl font-bold tracking-tight">Dashboard</h2>

            <div className="grid gap-4 md:grid-cols-3">
                <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Wallet Balance</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold">GHS {balance}</div>
                        <p className="text-xs text-muted-foreground">Available for purchases</p>
                        <Button asChild className="mt-4 w-full" size="sm">
                            <Link href="/dashboard/wallet">Top Up</Link>
                        </Button>
                    </CardContent>
                </Card>
                <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                        <CardTitle className="text-sm font-medium">Quick Order</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <div className="text-lg">Buy Data Bundle</div>
                        <Button asChild className="mt-4 w-full" size="sm">
                            <Link href="/dashboard/orders/new">Place Order</Link>
                        </Button>
                    </CardContent>
                </Card>
            </div>

            <div>
                <h3 className="text-xl font-semibold mb-4">Recent Orders</h3>
                {recentOrders.length === 0 ? (
                    <p className="text-gray-500">No orders yet.</p>
                ) : (
                    <div className="bg-white rounded-md border">
                        {/* Simple Table (Can use Shadcn Table component) */}
                        <div className="p-4">
                            <table className="w-full text-sm">
                                <thead>
                                    <tr className="border-b text-left">
                                        <th className="pb-2">ID</th>
                                        <th className="pb-2">Bundle</th>
                                        <th className="pb-2">Phone</th>
                                        <th className="pb-2">Cost</th>
                                        <th className="pb-2">Status</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {recentOrders.map((order) => (
                                        <tr key={order.id} className="border-b last:border-0 hover:bg-gray-50">
                                            <td className="py-2">{order.reference}</td>
                                            <td className="py-2">{order.bundle?.name || 'Bundle'}</td>
                                            <td className="py-2">{order.recipient_phone}</td>
                                            <td className="py-2">GHS {order.cost}</td>
                                            <td className="py-2 capitalize">{order.status}</td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}
